.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #6e644b;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@font-face {
  font-family: 'System';
  src: url('./assets/fonts/SeuratProB.otf') format('opentype');
  font-weight: normal;
  font-style: normal;
}

body {
  font-family: 'System', sans-serif;
  color: #6e644b;
}

@font-face {
  font-family: 'Dialogue';
  src: url('./assets/fonts/RodinBokutohProEB.otf') format('opentype');
  font-weight: normal;
  font-style: normal;
}

.image {
  max-width: 100%; /* Make sure the image does not exceed container width */
  height: auto; /* Maintain aspect ratio */
  object-fit: contain; /* Ensure the entire image is visible within the container */
}

.mainWallpaper {
  background-image: url(./assets/images/background.png);
  background-size: 10%;
  background-repeat: repeat;
  min-height: 100vh;
}

.background {
  background-color: #fcfbdf;
}